import { Personalization } from "@scripts/Personalization";
import { compareKeyboardState } from "@scripts/keyboard";

export const gifCom = new Personalization(
  "gif_com",
  /https:\/\/.*gif\.com\//i,
  {
    callback: () => {
      setTimeout(() => {
        let activated = false;

        window.addEventListener("keydown", (e) => {
          if (
            compareKeyboardState(e, [{ key: "Space", ctrl: true }]) &&
            !activated
          ) {
            activated = true;
            clearBody();
            console.log("personalized gif.com");

            window.addEventListener("paste", (e) => {
              let text = e.clipboardData?.getData("Text");

              if (text && text.match(/https?:\/\/[^ ]+/)) {
                let frame = document.createElement("iframe");
                frame.src = text;

                let div1 = document.createElement("div"),
                  div2 = document.createElement("div");
                div1.appendChild(frame);
                div1.appendChild(div2);

                div1.style.position = "relative";
                frame.style.height = "95%";
                frame.style.width = "100%";
                div2.style.height = "5%";
                div2.style.width = "100%";

                div1.style.border = "1px solid #656565";
                div1.style.borderRadius = "0.5rem";
                div1.style.overflow = "hidden";
                div2.style.backgroundColor = "#7d7d7d";

                div1.style.display = "flex";
                div1.style.flexFlow = "column nowrap";
                div1.style.alignItems = "center";
                div1.style.justifyContent = "center";
                div1.style.height = "40vh";
                div1.style.width = "12vw";

                let wide = false;
                div2.addEventListener("click", (e) => {
                  if (e.shiftKey) {
                    div1.parentElement!.removeChild(div1);
                  } else {
                    div1.style.width = wide ? "12vw" : "24vw";
                    wide = !wide;
                  }
                });
                div2.addEventListener("contextmenu", (e) => {
                  e.preventDefault();
                  div1.parentElement!.removeChild(div1);
                });

                document.body.appendChild(div1);
                document.body.style.display = "flex";
                document.body.style.flexFlow = "row wrap";
              }
            });
          }
        });
      }, 700);
    },
  }
);

function clearBody() {
  while (document.body.lastChild)
    document.body.removeChild(document.body.lastChild);
}
